@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700,900&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat';
}
.wgs-main {
  padding: 1rem;
}
a {
  text-decoration: none;
}

.wgs-header{position:relative;width:100%;height:100vh;color:white;display:flex;justify-content:center;align-items:center;flex-direction:column;-webkit-clip-path:polygon(0 0, 0 calc(100% - 128px), 25% 100%, 100% calc(100% - 128px), 100% 0);clip-path:polygon(0 0, 0 calc(100% - 128px), 25% 100%, 100% calc(100% - 128px), 100% 0);background-size:cover, cover}.wgs-header h3{font-size:1.75rem;margin:0;font-style:italic}.wgs-header-title-box{display:flex;flex-direction:row;justify-content:center;align-items:center}.wgs-header-title-box h1{font-size:4rem;margin:0}.wgs-header-title-box img{height:6rem;margin-right:1rem}.wgs-header-links{position:fixed;top:0;left:0;width:100%;display:flex;justify-content:center;background:linear-gradient(90deg, rgba(24,174,232,0.75), rgba(19,198,163,0.75));z-index:1}.wgs-header-links a{padding:1rem;cursor:pointer;color:white}@supports ((-webkit-backdrop-filter: blur(8px)) or (backdrop-filter: blur(8px))){.wgs-header-links{background:linear-gradient(90deg, rgba(24,174,232,0.5), rgba(19,198,163,0.5));backdrop-filter:blur(8px);-webkit-backdrop-filter:blur(8px)}}

.wgs-section-title{font-size:3rem}.wgs-section-horizontal-row{position:relative;display:flex;white-space:nowrap;width:100%;overflow:auto}.wgs-tile{display:inline-flex;flex-direction:row;width:384px;margin-bottom:1rem;box-shadow:0 10px 15px -3px rgba(0,0,0,0.1),0 4px 6px -2px rgba(0,0,0,0.05);color:black}.wgs-tile .wgs-tile-icon{width:128px;height:128px}.wgs-tile .wgs-tile-body{box-sizing:border-box;padding:1rem;width:256px;display:flex;flex-direction:column;white-space:normal}.wgs-tile .wgs-tile-body h2{margin:0}

